@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#FAFAFA}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.nav-bar{width:100%;padding-top:calc(max(25px, 9vh))}.nav-bar-content{height:9vh;width:100%;min-height:25px;background-color:#37BBBD;top:0;position:fixed;display:flex;z-index:2}.logo-container{width:20%;height:100%;position:relative}.links-container{width:80%;position:relative}.logo{height:90%;max-width:100%;left:15%;position:absolute;object-fit:contain}.nav-links{right:0;position:fixed;margin:0;padding:0;position:absolute;top:50%;transform:translateY(-50%)}.nav-links li{display:inline}a:link,a:visited{color:#FAFAFA;text-decoration:none}.link-title{margin-right:20px;font-weight:bold;font-size:18px;text-decoration:none}.link-title:hover{text-decoration:underline}

.App{text-align:center;width:100%;height:100%}

.footer{position:fixed;bottom:0;background-color:#00070F;color:#FAFAFA;width:100%;height:5%;min-height:25px;z-index:2}.footer-container{position:relative}.footer-lists{position:absolute;margin-top:0px;margin-bottom:0px;padding:0px;margin-left:2%;margin-right:2%;margin-top:0.5%}.language-list{left:0px}.language-list li{display:inline;padding-right:15px;font-size:12px}.contact-list{right:0px}.contact-list li{display:inline;padding-left:20px;font-size:25px}.a:link a:visited{margin:0px}

.projects{top:0px;margin-left:5%;margin-right:5%}.header{width:100%;display:flex}.title{font-weight:bold;font-size:32px;text-align:left;width:50%}.filters{display:flex;justify-content:flex-end;text-align:right;width:50%;font-size:25px}.filter-item{list-style-type:none;margin-left:5px}.projects-list{display:flex;flex-wrap:wrap;justify-content:space-evenly}.item{margin:2%}

.thumbnail{width:400px;height:260px;border-radius:10px;object-fit:cover}.gradient-background{position:relative;display:inline-block}.gradient-background:after{content:'';position:absolute;left:0;top:0;width:100%;height:99%;display:inline-block;border-radius:10px;box-shadow:0 1000px rgba(5,244,252,0.13) inset;background:linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(247,252,252,0.0416667) 40%, #37bbbd 100%);transition:background 1s ease-out}.gradient-background:hover .project-name{bottom:75%}.gradient-background:hover:after{background:linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(247,252,252,0.0416667) 40%, #37bbbd 100%),rgba(55,187,189,0.9);position:absolute}.project-name{position:absolute;bottom:0px;color:#FAFAFA;z-index:1;font-size:18px;font-weight:600;margin-left:5%;transition:1s}

